/* ------------------------------
    CSS Reset
------------------------------ */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

*, *:before, *:after {
	box-sizing: border-box;
  }

/* ------------------------------
    Base Styles
------------------------------ */

html, 
body {
	height: 100vh;
	min-height: 100vh;
	max-height: 100vh;
}

/* ------------------------------
    Font Styles
------------------------------ */

body {
	font-family: 'Courier New', Courier, monospace;
	font-size: 1rem;
	color: white;
}

/* ------------------------------
    Common
------------------------------ */

body {
	background: #000 url(/swapi-search/static/media/stars.eeab1111.jpg) no-repeat;
	background-size: cover;
}

.title {
	height: 10vh;
	width: inherit;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	font-size: 2.5rem;
	color: rgb(255, 249, 223);
}


.button {    
    border: 2px solid rgba(255, 219, 53, .5);

    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    color: rgba(255, 219, 53, .9);
    text-transform: uppercase;
    text-shadow: 0 1px 5px rgb(73, 63, 16);

    background: rgba(255, 219, 53, .1);

	cursor: pointer;

	transition: all .25s;
}

.button--center {
	width: 100%;
	height: 5vh;
	display: flex;
	justify-content: center;
}

.button:hover {
    border-color: rgba(255, 219, 53, 1);
    color: rgba(255, 219, 53, 1);
    background: rgba(255, 219, 53, .2)
}

