.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.menu {
    display: flex;
    flex-direction: column;

    height: 90vh;

    padding-bottom: .5rem;
}

.menu__header {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 10%;
}

.menu__list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;

    height: 90%;
}

.menu__button {
    height: 25%;
    width: 40%;
    border: 2px solid rgba(255, 219, 53, .5);

    margin: .25rem;

    font-family: 'Courier New', Courier, monospace;
    font-size: 1.25rem;
    color: rgba(255, 219, 53, .9);
    text-transform: uppercase;
    text-shadow: 0 1px 5px rgb(73, 63, 16);

    background: rgba(255, 219, 53, .1);

	cursor: pointer;

	transition: all .25s;
}

.menu__button:hover {
    border-color: rgba(255, 219, 53, 1);
    color: rgba(255, 219, 53, 1);
    background: rgba(255, 219, 53, .2)
}
.card {
    padding: 1rem 0;

    border-bottom: solid 1px white;

    font-size: 1.15rem;
}
.cardlist {
    height: 75vh;

    padding: .5rem 1rem;

    overflow-y: scroll;
}
.search {
    display: flex;
    justify-content: center;

    width: 100%;
    height: 10vh;
}

.search__input {
    width: 80%;
    max-height: 60px;
    border: 4px solid black;

    font-family: 'Courier New', Courier, monospace;
    color: black;
    font-size: .95rem;

    background: rgba(255, 219, 53, .7);
}
